.services {
  background-color: $color-black-pearl;
}

.services__container {
  padding-bottom: 140px;
  padding-top: 70px;

  @include vp-1023 {
    padding-bottom: 74px;
    padding-top: 55px;
  }

  @include vp-767 {
    padding-bottom: 86px;
    padding-top: 50px;
  }
}

.services ul {
  display: grid;
  gap: 30px;
  grid-auto-rows: minmax(650px, 1fr);
  grid-template-columns: repeat(3, minmax(150px, 350px));
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    min-height: 250px;
  }
}

.services h2 {
  color: $color-default-white;
  display: flex;
  font-family: $ff-robotocondensed;
  font-size: 36px;
  font-weight: 400;
  line-height: 120%;
  margin: 0;
  margin-bottom: 50px;
  min-height: 50px;

  @include vp-1023 {
    line-height: 140.63%;
    margin-bottom: 40px;
  }

  @include vp-767 {
    font-size: 28px;
    margin-bottom: 26px;
  }
}

.services ul li {
  background-size: cover;
  display: flex;
  position: relative;

  &:nth-child(1n) {
    @include graphic-retina("services/desktop/services-1", $color-deep-sky-blue);
  }

  &:nth-child(2n) {
    @include graphic-retina("services/desktop/services-2", $color-deep-sky-blue);
  }

  &:nth-child(3n) {
    @include graphic-retina("services/desktop/services-3", $color-deep-sky-blue);
  }

  @include vp-1023 {
    min-height: 250px;

    &:nth-child(1n) {
      @include graphic-retina("services/tablet/services-1", $color-deep-sky-blue);
    }

    &:nth-child(2n) {
      @include graphic-retina("services/tablet/services-2", $color-deep-sky-blue);
    }

    &:nth-child(3n) {
      @include graphic-retina("services/tablet/services-3", $color-deep-sky-blue);
    }
  }

  @include vp-767 {
    &:nth-child(1n) {
      @include graphic-retina("services/mobile/services-1", $color-deep-sky-blue);
    }

    &:nth-child(2n) {
      @include graphic-retina("services/mobile/services-2", $color-deep-sky-blue);
    }

    &:nth-child(3n) {
      @include graphic-retina("services/mobile/services-3", $color-deep-sky-blue);
    }
  }

  &::after {
    background-color: $color-deep-sky-blue;
    background-image: url("./../../img/svg/arrow.svg");
    background-position: calc(50% + 3px);
    background-repeat: no-repeat;
    background-size: 6px;
    border-radius: 3px 0 0 0;
    bottom: 0;
    content: "";
    height: 60px;
    position: absolute;
    right: 0;
    width: 60px;

    @include vp-767 {
      background-position: calc(50% + 2px);
      background-size: 4px;
      height: 40px;
      width: 40px;
    }
  }

  &::before {
    background: $color-black-pearl-50;
    content: "";
    height: 100%;
    position: absolute;
    top: 0;
    transition: 0.3s background-color;
    width: 100%;
  }

  @include hover-focus {
    &::before {
      background: none;
    }
  }
}

.services ul li a {
  @include default-focus($color-deep-sky-blue);
  color: $color-default-white;
  font-family: $ff-robotocondensed;
  font-size: 20px;
  line-height: 150%;
  margin: 0;
  padding: 46px 40px;
  position: relative;
  width: 100%;
  z-index: 1;

  @include vp-1023 {
    padding: 36px 45px;
  }

  @include vp-767 {
    font-size: 18px;
    padding: 30px;
  }
}
